import {
    GET_POSTS,
    GET_POST,
    UPDATE_LIKES,
    DELETE_POST,
    ADD_POST,
    POST_ERROR,
    ADD_COMMENT,
    DELETE_COMMENT,
} from '../actions/types'

const initialState = {
    posts: [],
    post: null,
    loading: true,
    error: {} // FIXME: why is error an empty object?
}

export default function postReducer(state = initialState, action) {
    const { type, payload } = action
    
    switch (type) {

        case GET_POSTS:
            return {
                ...state,
                posts: payload,
                loading: false,
            }
        
        case GET_POST:
            return {
                ...state,
                post: payload,
                loading: false,
            }
        
        case DELETE_POST:
            return {
                ...state,
                posts: state.posts.filter(post => post._id !== payload),
                loading: false
            }
        
        case ADD_POST:
            return {
                ...state,
                posts: [payload, ...state.posts],
                loading: false
            }
        
        case UPDATE_LIKES:
            return {
                ...state,
                posts: state.posts.map(post => post._id === payload._id ? { ...post, likes: payload.likes} : post),
                loading: false,
            }
        
        case ADD_COMMENT:
            return {
                ...state,
                post: { ...state.post, comments: payload },
                loading: false,
            }
        
        case DELETE_COMMENT:
            return {
                ...state,
                post: { ...state.post, comments: state.post.comments.filter(comment => comment._id !== payload) },
                loading: false,
            }
        
        case POST_ERROR:
            return {
                ...state,
                loading: false,
                error: payload
            }
        
        default:
            return state
        
    }
}